import React from 'react'

function Quiz({letter,setUserAnswer,PARFORM_AFFTER_STEPS}) {
  return (
          <div className='quiz'>
          <h3 className='quiz-text'>
            Letter "{letter}" is blong to 
          </h3>
          <div>
            <form>
              <input type='radio' name='answer' id='root-letter' value="0" onClick={(e)=> setUserAnswer(e.target.value)} />
              <label for="root-letter" className='option' onClick={()=> PARFORM_AFFTER_STEPS()}>Root Letters </label>
              <input type='radio' name='answer' id='sky-letter' value="1" onClick={(e)=> setUserAnswer(e.target.value)} />
              <label for="sky-letter" className='option' onClick={()=> PARFORM_AFFTER_STEPS()}>Sky Letters</label>
              <input type='radio' name='answer' id='grass-letter' value="2" onClick={(e)=> setUserAnswer(e.target.value)} />
              <label for="grass-letter" className='option' onClick={()=> PARFORM_AFFTER_STEPS()}>Grass Letters</label>
            </form>
            
            
          </div>
        </div>
  )
}

export default Quiz