import React from 'react'
import {BsFillPlayFill} from 'react-icons/bs'
function startButton() {
  return (
    <div className='start-button'>
          <p>Welcome to small quiz game</p>
          <button>
                    <BsFillPlayFill/>
                    Start Playing Now
          </button>
    </div>
  )
}

export default startButton